import React, { Component } from 'react';
import { Container, Offcanvas, Image, Navbar, Nav } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as $ from "jquery";
import { Link } from 'react-scroll';

class NavigationBar extends React.Component {

    render() {

        const fnCloseSideNav = () => {
            $(".btn-close").trigger("click");
        }

        let navbar = []

        let offset = []

        if (typeof window !== 'undefined') {

        if(window.location.pathname == "/"){
            navbar.push(
                <ul className="list-group list-group-horizontal d-none d-lg-inline-flex">
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);"><Link id='link_overview' to="overview" spy={true} smooth={true} offset={-50}>Overview</Link></a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);"><Link className='highlights' to="highlights" spy={true} smooth={true} offset={-50}>Highlights</Link></a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);"><Link className='amenities' to="amenities" spy={true} smooth={true} offset={-50}>Amenities</Link></a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);"><Link className='location' to="location" spy={true} smooth={true} offset={-50}>Location</Link></a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);"><Link className='plans' to="plans" spy={true} smooth={true} offset={-50}>Plans</Link></a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);"><Link className='price' to="price" spy={true} smooth={true} offset={-50}>Price</Link></a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);"><Link className='gallery' to="gallery" spy={true} smooth={true} offset={-50}>Gallery</Link></a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);"><Link className='video' to="video" spy={true} smooth={true} offset={-50}>Video</Link></a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="/nri">NRI Services</a></li>
                </ul>
            )
            offset.push(
                <Nav className="justify-content-end flex-grow-1 pe-3">
                    <Link to="overview" spy={true} smooth={true} offset={-50} onClick={() => fnCloseSideNav()}><Nav.Link href="javascript:void(0);">Overview</Nav.Link></Link>
                    <Link to="highlights" spy={true} smooth={true} offset={-50} onClick={() => fnCloseSideNav()}><Nav.Link href="javascript:void(0);">Highlights</Nav.Link></Link>
                    <Link to="amenities" spy={true} smooth={true} offset={-50} onClick={() => fnCloseSideNav()}><Nav.Link href="javascript:void(0);">Amenities</Nav.Link></Link>
                    <Link to="location" spy={true} smooth={true} offset={-50} onClick={() => fnCloseSideNav()}><Nav.Link href="javascript:void(0);">Location</Nav.Link></Link>
                    <Link to="plans" spy={true} smooth={true} offset={-50} onClick={() => fnCloseSideNav()}><Nav.Link href="javascript:void(0);">Plans</Nav.Link></Link>
                    <Link to="price" spy={true} smooth={true} offset={-50} onClick={() => fnCloseSideNav()}><Nav.Link href="javascript:void(0);">Price</Nav.Link></Link>
                    <Link to="gallery" spy={true} smooth={true} offset={-50} onClick={() => fnCloseSideNav()}><Nav.Link href="javascript:void(0);">Gallery</Nav.Link></Link>
                    <Link to="video" spy={true} smooth={true} offset={-50} onClick={() => fnCloseSideNav()}><Nav.Link href="javascript:void(0);">Video</Nav.Link></Link>
                    <Nav.Link href="/nri">NRI Services</Nav.Link>
                </Nav>
            )
        }else{
            const fnHomeRedirect = (section) => {
                localStorage.setItem('scrollToSection', section);
                window.location.href = '/';
            }
            navbar.push(
                <ul className="list-group list-group-horizontal d-none d-lg-inline-flex">
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);" onClick={() => fnHomeRedirect('overview')} >Overview</a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);" onClick={() => fnHomeRedirect('highlights')}>Highlights</a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);" onClick={() => fnHomeRedirect('amenities')}>Amenities</a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);" onClick={() => fnHomeRedirect('location')}>Location</a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);" onClick={() => fnHomeRedirect('plans')}>Plans</a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);" onClick={() => fnHomeRedirect('price')}>Price</a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);" onClick={() => fnHomeRedirect('gallery')}>Gallery</a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="javascript:void(0);" onClick={() => fnHomeRedirect('video')}>Video</a></li>
                    <li className="list-group-item border-0 bg-transparent text-light"><a href="/nri">NRI Services</a></li>
                </ul>
            )
            offset.push(
                <Nav className="justify-content-end flex-grow-1 pe-3">
                    <Nav.Link href="javascript:void(0);" onClick={() => fnHomeRedirect('overview')}>Overview</Nav.Link>
                    <Nav.Link href="javascript:void(0);" onClick={() => fnHomeRedirect('highlights')}>Highlights</Nav.Link>
                    <Nav.Link href="javascript:void(0);" onClick={() => fnHomeRedirect('amenities')}>Amenities</Nav.Link>
                    <Nav.Link href="javascript:void(0);" onClick={() => fnHomeRedirect('location')}>Location</Nav.Link>
                    <Nav.Link href="javascript:void(0);" onClick={() => fnHomeRedirect('plans')}>Plans</Nav.Link>
                    <Nav.Link href="javascript:void(0);" onClick={() => fnHomeRedirect('price')}>Price</Nav.Link>
                    <Nav.Link href="javascript:void(0);" onClick={() => fnHomeRedirect('gallery')}>Gallery</Nav.Link>
                    <Nav.Link href="javascript:void(0);" onClick={() => fnHomeRedirect('video')}>Video</Nav.Link>
                    <Nav.Link href="/nri">NRI Services</Nav.Link>
                </Nav>
            )
        }
    }

        return (
            <>

                <Navbar bg="dark" expand={false} sticky="top">
                    <Container>
                        <Navbar.Brand href="/"><Image src={this.props.logo} width={150} /></Navbar.Brand>

                        {navbar}

                        <Navbar.Toggle className="d-lg-none" aria-controls="offcanvasNavbar" />
                        <Navbar.Offcanvas
                            id="offcanvasNavbar"
                            aria-labelledby="offcanvasNavbarLabel"
                            placement="end"
                        >
                            <Offcanvas.Header closeButton>
                                <Offcanvas.Title id="offcanvasNavbarLabel"><Image src={this.props.logo} width={150} /></Offcanvas.Title>
                            </Offcanvas.Header>
                            <Offcanvas.Body>
                                {offset}
                            </Offcanvas.Body>
                        </Navbar.Offcanvas>
                    </Container>
                </Navbar>

            </>
        )
    }
}
export default NavigationBar;